/* styles.css */

.content {
  @apply p-8 flex-1 overflow-y-auto h-[calc(100vh-48px)];
}

.content h1 {
  @apply text-4xl font-semibold pb-2 [&:not(:first-child)]:mt-4 text-color-text;
}

.content h2 {
  @apply text-2xl font-semibold pb-2 [&:not(:first-child)]:mt-8 mb-3 text-color-text border-b border-color-border;
}

.content h3 {
  @apply text-xl font-semibold pb-2 [&:not(:first-child)]:mt-8 mb-3 text-color-text;
}

.content h4 {
  @apply text-lg font-semibold pb-2 [&:not(:first-child)]:mt-8 mb-3 text-color-text;
}

.content h5 {
  @apply text-base font-semibold pb-2 [&:not(:first-child)]:mt-8 mb-3 text-color-text;
}

.content h6 {
  @apply text-sm font-semibold pb-2 [&:not(:first-child)]:mt-8 mb-3 text-color-text-subtle;
}

.content p {
  @apply [&:not(:first-child)]:mt-4 leading-relaxed text-color-text;
}

.content a {
  @apply text-color-hyperlink hover:text-color-primary-hover hover:underline transition-none;
}

.content hr {
  @apply my-6 border-color-border;
}


.inline-code {
  color: rgb(248, 248, 242);
  background: rgb(43, 43, 43);
  font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  overflow-wrap: normal;
  line-height: 1.5;
  tab-size: 4;
  hyphens: none;
  padding: 1px 3px;
  margin: 0px;
  overflow: auto;
  border-radius: 0 .3em;
}

/* code-block */
.code-block-gdscript {
  @apply m-0 p-4 bg-color-gd-bg border-x-2 border-b-2 border-color-code-header rounded-b-md rounded-t-none;
}

.language-gdscript {
  @apply text-color-gd-default;
}

.token.comment {
  @apply text-color-gd-comment;
}

.token.keyword {
  @apply text-color-gd-keyword;
}

.token.class-name {
  @apply text-color-gd-class-name;
}

.token.function {
  @apply text-color-gd-function;
}

.token.punctuation {
  @apply text-color-gd-punctuation;
}

.token.operator {
  @apply text-color-gd-operator;
}

.token.string {
  @apply text-color-gd-string;
}

.token.number {
  @apply text-color-gd-number;
}
